import { html } from 'lit';
import type { Meta, StoryObj } from '@storybook/web-components';

const meta: Meta = {
  title: 'charts/PieChart',
  component: 'pie-chart', // Use string name, not type reference
  tags: ['autodocs'],
  argTypes: {
    data: {
      control: 'object',
    },
  },
};

export default meta;

type PieChartData = { label: string; value: number };
type Story = StoryObj<{ data: PieChartData[] }>;

export const Default: Story = {
  args: {
    data: [
      { label: 'A', value: 30 },
      { label: 'B', value: 70 },
      { label: 'C', value: 45 },
    ],
  },
  render: (args) => html`<pie-chart .data=${args.data}></pie-chart>`,
};
